const PowerpointPdf = {
  async render() {
    return `
    <section class="container d-flex justify-content-center my-4 py-5">
            <div class="bg-primary text-center rounded py-5 my-4 col-lg-8">
                <h1 class="display-4 fw-bold">Powerpoint to PDF</h1>
                <img data-src="/icons/LOGO PPT PDF.webp" alt="Powerpoint to PDF" class="w-25 img-fluid lazyload">
                <div class="row justify-content-center">
                    <label for="formFileLg" class="form-label fs-5 py-3">Silahkan Upload File</label>
                    <div class="px-5 mx-5"><input class="form-control fs-5" id="formFileLg" type="file" placeholder="Pilih File"></div>
                    <div class="pt-3"><button type="button" class="btn btn-success fs-5">Convert</button></div>
                </div>
            </div>
        </section>
    `;
  },

  async afterRender() {
    // Fungsi ini akan dipanggil setelah render()
  },
};

export default PowerpointPdf;
